import React, { useCallback, useContext, useState, useEffect } from 'react'
import PropTypes from 'prop-types'
import { ExtensionContext2 } from '@looker/extension-sdk-react'
import { LookerEmbedSDK } from '@looker/embed-sdk'
import { DashboardContext } from '../providers/EmbeddedDashboardProvider'
import { EmbedContainer } from './EmbedContainer'

export const EmbedDashboard = ({ id }) => {
  const { extensionSDK } = useContext(ExtensionContext2)
  const { options, saveOptions } = useContext(DashboardContext)
  const [dashboard, setDashboard] = useState()

  useEffect(() => {
    if (options && dashboard) {
      console.log('setOptions', { options })
      dashboard.setOptions(options)
    }
  }, [options])

  const setupDashboard = (dashboard) => {
    setDashboard(dashboard)
  }

  const dashboardLoaded = (event) => {
    const { dashboard } = event
    const { options } = dashboard
    saveOptions(options)
  }

  const embedCtrRef = useCallback(
    (el) => {
      if (el && id) {
        const hostUrl = extensionSDK.lookerHostData?.hostUrl
        if (hostUrl) {
          LookerEmbedSDK.init(hostUrl)
          LookerEmbedSDK.createDashboardWithId(id)
            .withNext()
            .appendTo(el)
            .on('dashboard:loaded', dashboardLoaded)
            .build()
            .connect()
            .then(setupDashboard)
            .catch((error) => {
              console.error('Connection error', error)
            })
        }
      }
    },
    [id]
  )

  return <EmbedContainer ref={embedCtrRef} />
}

EmbedDashboard.propTypes = {
  id: PropTypes.string.isRequired,
}
