import React, { useContext, useEffect, useState } from 'react'
import {
  IconButton,
  ButtonToggle,
  ButtonItem,
  Space,
  SpaceVertical,
} from '@looker/components'
import { Refresh } from '@styled-icons/material'
import { DashboardContext } from '../providers/EmbeddedDashboardProvider'
import { EmbedDashboard } from './EmbedDashboard'

const AVAILABLE_VIZ_TYPES = [
  'looker_bar',
  'looker_column',
  'looker_line',
  'looker_area',
  'looker_scatter',
  'looker_pie',
  'table',
  'looker_grid',
]

const dashboardId = '19'

const SelectVizScene = () => {
  const { restoreOptions, updateDashboardId, tiles, updateTiles } =
    useContext(DashboardContext)
  const [vizType, setVizType] = useState()

  useEffect(() => {
    updateDashboardId(dashboardId)
  }, [])

  useEffect(() => {
    if (vizType && tiles) {
      const updates = tiles.map(({ id }) => ({
        id,
        data: { vis_config: { type: vizType } },
      }))
      console.log({ updates })
      updateTiles(updates)
    }
  }, [vizType])

  const refresh = () => {
    setVizType(undefined)
    restoreOptions()
  }

  return (
    <SpaceVertical>
      <Space>
        <IconButton icon={<Refresh />} onClick={refresh} />
        <ButtonToggle value={vizType} onChange={setVizType}>
          {AVAILABLE_VIZ_TYPES.map((availableVizType) => (
            <ButtonItem key={availableVizType}>{availableVizType}</ButtonItem>
          ))}
        </ButtonToggle>
      </Space>
      <EmbedDashboard id={dashboardId} />
    </SpaceVertical>
  )
}

export default SelectVizScene
